body {
    font-size: 13px;
    line-height: 1.8;
    background-color: #212b2d; 
    font-weight: 400;
    font-family: Poppins;
    display: block;
    /* margin: 8px; */
}
/* .signup {
    margin-bottom: 80px;
} */
section {
    display: block;
}
.container {
    background: #fff;
    margin: 0 auto;
    /* -webkit-box-shadow: 0px 15px 16.83px 0.17px rgba(0, 0, 0, 0.05); */
    /* -webkit-border-radius: 20px; */
}
div {
    display: block;
}
.signup-content {
    -webkit-flex-direction: column;
    -webkit-justify-content: center;
    padding: 75px 0;
    display: -webkit-flex;
}
.signup-form {
    width: auto;
    margin-left: 0px;
    margin-right: 0px;
    padding-left: 0px;
    /* box-sizing: border-box; */
    padding: 0 30px;
    overflow: hidden;
}
.form-title {
    margin-bottom: 33px;
    text-align: center;
}
h2 {
    line-height: 1.66;
    margin: 0;
    padding: 0;
    font-weight: bold;
    color: #222;
    font-family: Poppins;
    font-size: 56px;
    display: block;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
.register-form {
    width: 100%;
}
form {
    display: block;
    margin-top: 0em;
}
.form-group {
    position: relative;
    /* margin-bottom: 5px; */
    overflow: hidden;
}
label {
    position: absolute;
    left: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
    color: #222;
    cursor: default;
}
.material-icons-name {
    font-size: 38px;
}
.zmdi {
    display: inline-block;
    font: normal normal normal 14px/1 'Material-Design-Iconic-Font';
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
}
.zmdi-account:before {
    content: '\f207';
}
.zmdi-email:before {
    content: '\f15a';
}
.zmdi-lock:before {
    content: '\f191';
}
.signup-input {
    width: 100%;
    display: block;
    border: none;
    border-bottom: 1px solid #999;
    padding: 6px 30px;
    box-sizing: border-box;
    outline: none;
    -webkit-appearance: unset !important;
    background-color: transparent;
    -webkit-rtl-ordering: logical;
    cursor: text;
    text-rendering: auto;
    color: initial;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    text-align: start;
    margin: 0em;
    font: 400 11px system-ui;
    -webkit-writing-mode: horizontal-tb !important;
}
.form-group:last-child {
    margin-bottom: 0px;
}
.form-button {
    text-align: center;
}
.form-submit {
    display: inline-block;
    /* background: #6dabe4; */
    color: #212b2d;
    border-bottom: none;
    width: auto;
    padding: 15px 39px;
    -webkit-border-radius: 5px;
    margin-top: 25px;
    cursor: pointer;
}

.account-a {
    font-size: 18px;
    text-decoration: none;
    cursor: pointer;
    color: #E75B00;
    margin-left: 5px4
}
#logo {
    display: flex;
    justify-content: center;
  
  }